import React from "react";

const CountryList = props => {
  const { country, confirmed, deaths, recovered } = props.data[0];
  return (
    <table width="100%">
      <thead>
        <tr>
          <th>Country</th>
          <th>Total Cases</th>
          <th>Total Death</th>
          <th>Total Recovered</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{country}</td>
          <td>{confirmed}</td>
          <td>{deaths}</td>
          <td>{recovered}</td>
        </tr>
      </tbody>
    </table>
  );
};

export default CountryList;
